.navigation {
  padding: 26px 165px;
  background-color: black;
  display: flex;
  align-items: center;
  gap: 94px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 3;

  .logo {
    color: #fff;
    font-size: 48px;
    font-weight: 700;
    line-height: 48px;
    letter-spacing: -2px;
    text-align: left;
    text-decoration: none;
  }

  ul {
    display: flex;
    gap: 60px;
    list-style: none;
    margin-right: auto;

    li {
      a,
      button {
        color: #7d828f;
        font-size: 18px;
        font-weight: 700;
        line-height: 25px;
        letter-spacing: 0px;
        text-align: left;
        text-decoration: none;
        background-color: transparent;
        border: none;
        cursor: pointer;
        text-transform: uppercase;
        transition: all 0.3s;

        &:hover {
          color: white;
        }
      }

      &.login {
        a,
        button {
          text-transform: uppercase;
          color: orange;
        }
      }
    }
  }
}

.updated {
  position: fixed;
  z-index: 100;
  top: 105px;
  left: 0;
  right: 0;
  padding: 10px;
  background-color: orange;
  color: white;
  text-align: center;
  text-transform: uppercase;
}

.cart {
  background-color: transparent;
  border: none;
  cursor: pointer;
  position: relative;

  span {
    position: absolute;
    width: 20px;
    height: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: content-box;
    border-radius: 50%;
    background-color: orange;
    top: -3px;
    right: 0;
    color: #000;
    font-weight: 700;
  }

  img {
    width: 30px;
    padding: 10px;
    box-sizing: content-box;
  }
}

.mobile-navigation {
  display: none;
  .open {
    background-color: black;
    position: fixed;
    top: 0;
    width: 100vw;
    height: 100vh;
    z-index: 999;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    padding-top: 30vh;

    li {
      padding: 30px;
      border-bottom: 2px solid white;
      width: 100vw;

      a,
      button {
        background-color: transparent;
        border: none;
        text-transform: uppercase;
        color: #fff;
        font-size: 45px;
        font-weight: 700;
        line-height: 45px;
        letter-spacing: 0px;
        text-align: left;
        text-decoration: none;
        background-color: transparent;
        border: none;
        cursor: pointer;
      }

      button {
        color: orange;
      }
    }
  }

  .mobile {
    position: fixed;
    top: 0px;
    right: 0;
    padding: 30px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    z-index: 1000;
    background-color: transparent;
    border: none;

    .logo {
      color: orange;
      font-size: 48px;
      font-weight: 700;
      line-height: 48px;
      letter-spacing: -2px;
      text-align: left;
      text-decoration: none;
    }
    .menu_icon {
      width: 60px;
      height: 60px;
      z-index: 10;
      cursor: pointer;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 10px;
      background: rgba(255, 255, 255, 0.21);
      box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
      border-radius: 16px;
      backdrop-filter: blur(5px);
      border: 1px solid rgba(255, 255, 255, 0.3);
      border-radius: 50%;
    }

    .menu_icon span {
      position: absolute;
      display: block;
      height: 3px;
      width: 30px;
      background-color: #c3c3c3;
      transform: rotate(0);
      transition: all 200ms cubic-bezier(0.895, 0.03, 0.685, 0.22);
    }

    span.one {
      top: 20px;
    }

    span.two {
      top: 27.5px;
    }

    span.three {
      top: 35px;
    }
    .clicked .one {
      transform: translateY(7.5px) rotate(45deg);
    }

    .clicked .two {
      opacity: 0;
    }

    .clicked .three {
      transform: translateY(-7.5px) rotate(-45deg);
    }
  }
}

@media only screen and (max-width: 1200px) {
  .navigation {
    padding: 26px 100px;
  }
}

@media only screen and (max-width: 1000px) {
  .navigation {
    display: none;
  }
  .mobile-navigation {
    display: initial;
  }

  .updated {
    top: 0px;
  }
}
