.header {
  background: linear-gradient(rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.7)),
    url(../../../../public/assets/home/header-bg.jpg);
  background-size: cover;
  background-position: top;
  background-repeat: no-repeat;
}

.header .text {
  padding-top: 350px;
  padding-left: 165px;
  padding-bottom: 160px;
  color: white;
}

.text a {
  padding: 15px 30px;
  font-size: 13px;
  font-weight: 700;
  line-height: 18px;
  letter-spacing: 1px;
  text-align: left;
  background-color: orange;
  border: 1px solid orange;
  color: #fff;
  text-transform: uppercase;
  text-decoration: none;

  &:hover {
    background-color: #fff;
    border: 1px solid orange;
    color: orange;
  }
}

.text h5 {
  font-size: 14px;
  font-weight: 400;
  line-height: 19px;
  letter-spacing: 10px;
  text-align: left;
  text-transform: uppercase;
  margin-bottom: 24px;
}

.text h1 {
  font-size: 56px;
  font-weight: 700;
  line-height: 58px;
  letter-spacing: 2px;
  text-align: left;
  margin-bottom: 24px;
  text-transform: uppercase;
  max-width: 400px;
}

.text p {
  font-size: 15px;
  font-weight: 500;
  line-height: 25px;
  letter-spacing: 0px;
  text-align: left;
  margin-bottom: 40px;
  max-width: 400px;
}

@media only screen and (max-width: 1200px) {
  .header .text {
    padding-left: 100px;
    padding-top: 300px;
    padding-bottom: 130px;
  }
}

@media only screen and (max-width: 1000px) {
  .header .text {
    padding-left: 50px;
  }
}

@media only screen and (max-width: 800px) {
  .header {
    padding: 150px 50px;
    background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.3)),
      url(../../../../public/assets/home/about.jpg);
    background-size: cover;
    background-position: center;

    .text {
      padding: 0;

      h5 {
        margin-bottom: 15px;
      }

      h1 {
        font-size: 42px;
        line-height: 50px;
        margin-bottom: 15px;
      }

      p {
      }
    }
  }
}
